eCaseExecutor';\nimport { IViewModel } from '../../../domain/viewModel/IViewModel';\nimport { IViewModelManager } from '../../../domain/viewModel/IViewModelManager';\nimport OAuthSettings from \"../components/application/oAuthSettings.vue\";\nimport CustomSelect from \"../components/customSelect.vue\";\n\n\nconst viewModel = container.resolve<IViewModel>(IViewModel);\nconst viewModelManager = container.resolve<IViewModelManager>(IViewModelManager);\nconst studioModelBasicsImpl = container.resolve<StudioModelBasicsImpl>(StudioModelBasicsImpl);\nconst asyncComponentCreator = container.resolve<IAsyncComponentCreator>(IAsyncComponentCreator);\nconst dialog = container.resolve<IDialog>(IDialog);\n\ninterface IProps {\n  functions: IAppSettingsEditorProps[\"functions\"];\n  callbacks: IAppSettingsEditorProps[\"callbacks\"];\n}\n\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst objectUseCase = container.resolve<IObjectUseCase>(IObjectUseCase);\nconst props = defineProps<IProps>();\nconst settingsModel = ref<IAppSettings>();\nlet oldSettingsModel: IAppSettings = { general: {} };\nlet newSettingsModel: IAppSettings = { general: {} };\nconst showingTab = ref(<\"General\" | \"UI Theme\" | \"oAuth\">(\"General\"));\nconst app = computed(() => viewModel.apps.find((app) => app.ID == viewModel.studio.appId));\nconst oAuthSettingsModel = computed(() => {\n  if (settingsModel.value!.oAuthSettings) {\n    return settingsModel.value!.oAuthSettings;\n  }\n  let settingsIAMV2: ISettingsIAMV2 = { active: false, idendityProviderURL: '', clientId: '', realm: '', isRealmFromQS: false, localeKey: '', isLocaleKeyFromQS: false, subdomainSource: \"constant\", subdomainValue: '', unsecurePages: [''], redirectUri: '', preserveTokens: false }\n  return settingsIAMV2;\n})\n\n\nconst defaultWebLottieName = computed(() => {\n  let lottieID: string;\n  let lottiePath = settingsModel.value?.general.webLoading;\n  if (lottiePath && lottiePath.length > 0) {\n    lottieID = studioModelBasicsImpl.getModelIdFromPathStructure(lottiePath, 'lottie');\n    let retVal = viewModel.studio.items.find(i => i.ID == lottieID)?.name;\n    return retVal;\n  }\n  return \"\";\n})\n\nconst defaultMobileLottieName = computed(() => {\n  let lottieID: string;\n  let lottiePath = settingsModel.value?.general.mobileLoading;\n  if (lottiePath && lottiePath.length > 0) {\n    lottieID = studioModelBasicsImpl.getModelIdFromPathStructure(lottiePath, 'lottie');\n    let retVal = viewModel.studio.items.find(i => i.ID == lottieID)?.name;\n    return retVal;\n  }\n  return \"\";\n})\n\nconst modelMap = computed(() => viewModel.studio.items.map(model => {\n  return { ID: model.ID, name: model.name, type: model.objectType };\n}))\n\nfunction inputChanged() {\n  if (newSettingsModel.general.mobileLoading == oldSettingsModel.general.mobileLoading && newSettingsModel.general.siteTitle == oldSettingsModel.general.siteTitle &&\n    newSettingsModel.general.startScreen == oldSettingsModel.general.startScreen && newSettingsModel.general.webLoading == oldSettingsModel.general.webLoading &&\n    newSettingsModel.oAuthSettings == oldSettingsModel.oAuthSettings)\n    props.callbacks.modified({ model: false });\n  else\n    props.callbacks.modified({ model: true });\n}\n\nprops.functions.getModel = async () => {\n  const model = JSON.stringify(settingsModel.value!);\n  return { model, state: {} };\n};\nprops.functions.setModel = async (model, state) => {\n  settingsModel.value = JSON.parse(model);\n  oldSettingsModel = JSON.parse(model);\n  newSettingsModel = JSON.parse(model);\n}\n\nconst lottiesModels = computed(() => {\n  const models: Array<String> = [];\n  viewModelManager.traverseChildren(viewModel.studio.items, (item) => {\n    if (item.objectType == \"model\" && item.modelType == \"lottie\") { models.push(item.name); }\n    return false;\n  });\n  return models;\n});\n\nfunction updateApp() {\n  let appParam = viewModel.apps.find(i => i.ID == viewModel.studio.appId) as IApplication;\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../components/application/addOrUpdateApp.vue`)),\n    { closable: true, title: \"Update Application\" },\n    { app: appParam }\n  );\n}\n\nfunction changeAppLogo() {\n  let appParam = viewModel.apps.find(i => i.ID == viewModel.studio.appId);\n  dialog.showDialog(\n    defineAsyncComponent(() => import(`../components/dialogs/appLogoDialog.vue`)),\n    { closable: true, title: \"Select App Image\", width: \"530px\" },\n    { app: appParam }\n  );\n}\n\nfunction onWebLottieSelect(lottieName: string) {\n  // settingsModel.value!.general.webLoading = lottieName;\n  const modelID = getModelIDInModelMapByName(lottieName);\n  const val = studioModelBasicsImpl.createPathStructureByModelId(modelID!, 'lottie' as ModelType);\n  settingsModel.value!.general.webLoading = val;\n  newSettingsModel.general.webLoading = val;\n  inputChanged();\n}\n\nfunction onMobileLottieSelect(lottieName: string) {\n  const modelID = getModelIDInModelMapByName(lottieName);\n  const val = studioModelBasicsImpl.createPathStructureByModelId(modelID!, 'lottie' as ModelType);\n  settingsModel.value!.general.mobileLoading = val;\n  newSettingsModel.general.mobileLoading = val;\n  inputChanged();\n}\n\nfunction onInputSiteTitle($event: any) {\n  settingsModel.value!.general.siteTitle = $event.target.value;\n  newSettingsModel.general.siteTitle = $event.target.value;\n  inputChanged();\n}\nfunction onOAuthSettingsChange(params: { [key: string]: string }) {\n  const changedKey = Object.keys(params)[0];\n\n  (<any>oAuthSettingsModel.value)[changedKey] = params[changedKey];\n  settingsModel.value!.oAuthSettings = oAuthSettingsModel.value;\n  newSettingsModel.oAuthSettings = oAuthSettingsModel.value;\n  inputChanged();\n}\n\nfunction importDialog() {\n  dialog.showDialog<{ param: INewItemPropsInline }>(\n    asyncComponentCreator.createInputModelDialog(),\n    { closable: true, title: \"Upload Local File\", closeOnOutClick: true, maxHeight: \"430px\", width: \"600px\" },\n    { param: { ownerId: viewModel.studio.appId, path: \"/\" } },\n  );\n}\n\nfunction addEventListeners() {\n  document.getElementById(\"add-web-lottie\")!.addEventListener(\"click\", importDialog);\n  document.getElementById(\"add-mobile-lottie\")!.addEventListener(\"click\", importDialog);\n}\n\nfunction selectTab(val: \"General\" | \"UI Theme\" | \"oAuth\") {\n  showingTab.value = val;\n}\n\nfunction getModelNameInModelMapByID(ID?: string) {\n  return modelMap.value.find(i => i.ID == ID)?.name;\n}\n\nfunction getModelIDInModelMapByName(name?: string) {\n  return modelMap.value.find(i => i.name == name)?.ID;\n}\n\n\n</script>\n\n\n<template>\n  <div class=\"maindiv\">\n    <div class=\"header\">\n\n      <span class=\"headertext\"><b>{{ showingTab == \"oAuth\" ? \"Configuration\" : showingTab }}</b></span>\n    </div>\n    <div class=\"leftnav\">\n      <div :class=\"'leftnavbutton' + (showingTab === 'General' ? ' selected' : '')\" @click=\"selectTab('General')\">\n        <i class=\"mdi mdi-cogs leftnavbuttonicon\"></i>\n        <span :class=\"'leftnavbuttontext' + (showingTab === 'General' ? ' selected' : '')\">General</span>\n      </div>\n      <!-- <div :class=\"'leftnavbutton' + (showingTab === 'UI Theme' ? ' selected' : '')\" @click=\"selectTab('UI Theme')\">\n         <i class=\"mdi mdi-brush-outline leftnavbuttonicon\"></i>\n         <span :class=\"'leftnavbuttontext' + (showingTab === 'UI Theme' ? ' selected' : '')\">UI Theme</span>\n      </div> -->\n      <div :class=\"'leftnavbutton' + (showingTab === 'oAuth' ? ' selected' : '')\" @click=\"selectTab('oAuth')\">\n        <i class=\"mdi mdi-lock-outline leftnavbuttonicon\"></i>\n        <span :class=\"'leftnavbuttontext' + (showingTab === 'oAuth' ? ' selected' : '')\">oAuth</span>\n      </div>\n    </div>\n    <div class=\"content\">\n      <div v-if=\"showingTab === 'General'\">\n        <div class=\"contentinnerdiv\" style=\"top:0px;\">\n          <p class=\"combotext\"><b>Application name</b></p>\n          <input class=\"textbox\" style=\"background: #E3E3E3;\" readonly=\"true\" disabled=\"true\" :value=\"app?.name\" />\n          <div class=\"appnameediticon\"><i class=\"mdi mdi-pencil\" @click=\"updateApp\"></i></div>\n        </div>\n        <div class=\"contentinnerdiv\" style=\"top:86px;\">\n          <p class=\"combotext\"><b>Site title</b></p>\n          <i class=\"mdi mdi-information informationicon\"></i>\n          <span class=\"info-tooltip\" style=\"left: 81px; top: 17px; width: 259px;\">\n            This title will be displayed in the web browser.\n          </span>\n          <input class=\"textbox\" type=\"text\" @input=\"onInputSiteTitle\" :value=\"settingsModel?.general.siteTitle\" />\n        </div>\n        <!-- <div class= \"contentinnerdiv\" style=\"top:176px;\">\n          <p class=\"combotext\"><b>Start screen</b></p>\n          <CustomSelect @input=\"onStartScreenSelect\" \n                :options = uiPageModels\n                :default = \"(uiPageModels.find(i => i == settingsModel!.general!.startScreen) as string)\"\n                :selectedClass = \"'selected-lottie'\"\n                class=\"select-lottie\">\n          </CustomSelect>\n        </div> -->\n\n        <div class=\"contentinnerdiv\" style=\"top:176px;\">\n          <!-- <div class= \"contentinnerdiv\" style=\"top:262px;\"> -->\n          <p class=\"combotext\"><b>Web loading animation</b></p>\n          <CustomSelect @input=\"onWebLottieSelect\" @click=\"addEventListeners()\" :options=\"lottiesModels\"\n            :placeholder=\"'Select'\" :default=\"defaultWebLottieName\" :selected-option=\"defaultWebLottieName\"\n            :iconInSelect=\"true\" :selectedClass=\"'selected-lottie'\" :extra-button=\"true\"\n            :buttons=\"[{ buttonText: '+ Add New', buttonClass: 'add-new-lottie-in-options', buttonID: 'add-web-lottie' }]\"\n            class=\"select-lottie\">\n          </CustomSelect>\n        </div>\n        <div class=\"contentinnerdiv\" style=\"top:262px;\">\n          <!-- <div class= \"contentinnerdiv\" style=\"top:348px;\"> -->\n          <p class=\"combotext\"><b>Mobile loading animation</b></p>\n          <CustomSelect @input=\"onMobileLottieSelect\" @click=\"addEventListeners()\" :options=\"lottiesModels\"\n            :placeholder=\"'Select'\" :default=\"defaultMobileLottieName\" :selected-option=\"defaultMobileLottieName\"\n            :iconInSelect=\"true\" :selectedClass=\"'selected-lottie'\" :extra-button=\"true\"\n            :buttons=\"[{ buttonText: '+ Add New', buttonClass: 'add-new-lottie-in-options', buttonID: 'add-mobile-lottie' }]\"\n            class=\"select-lottie\">\n          </CustomSelect>\n        </div>\n        <div class=\"appimagediv\">\n          <p class=\"combotext\"><b>App Image</b></p>\n          <div v-if=\"app?.logoInfo && app?.synced\" class=\"appimagelogodiv\" @click=\"changeAppLogo\">\n            <img :src=\"`${app?.logoInfo.hostUrl}${app?.logoInfo.path}?c=${app?.logoInfo.updateDate}`\" style=\"opacity: 1;\"\n              alt=\"ide-logo\" class=\"title-logo\" />\n          </div>\n          <div v-else class=\"appimagelogodiv\" @click=\"changeAppLogo\">\n            <img src=\"../../../images/ide5-logo.svg\" alt=\"ide-logo\" class=\"title-logo\" />\n          </div>\n        </div>\n      </div>\n      <div v-if=\"showingTab === 'UI Theme'\">\n      </div>\n      <div v-if=\"showingTab === 'oAuth'\" style=\"padding: 0 40px 20px; \">\n        <OAuthSettings @getSettingsModel=\"onOAuthSettingsChange\" :oAuthSettings=\"oAuthSettingsModel\"></OAuthSettings>\n      </div>\n    </div>\n\n    <!--<div class=\"footer\">\n       <div class=\"reset\">Reset</div>\n      <div class=\"savechanges\">SAVE CHANGES</div> \n    </div>-->\n  </div>\n</template>\n\n\n\n<style scoped>\n* {\n  font-family: 'Roboto';\n}\n\n.appnameediticon {\n  cursor: pointer;\n  width: 24px;\n  height: 24px;\n\n  font-family: 'Material Design Icons';\n  font-style: normal;\n  font-weight: 400;\n  font-size: 24px;\n  line-height: 24px;\n  /* identical to box height */\n  text-align: center;\n  /* Plateau Navy */\n  color: #151233;\n  /* Inside auto layout */\n  flex: none;\n  order: 0;\n  flex-grow: 0;\n  position: absolute;\n  margin-left: 410px;\n  top: 30px;\n}\n\n.selected-lottie {\n  background-color: transparent;\n  border-radius: 4px;\n  border: 1px solid #BCBCBC;\n  color: #212121;\n  padding-left: 12px;\n}\n\n.select-lottie {\n  top: 22px;\n  position: absolute;\n  width: 400px;\n  height: 40px;\n  font-size: 15px;\n  flex: none;\n  order: 1;\n  align-self: stretch;\n  flex-grow: 0;\n  border: 1px solid #BCBCBC;\n  border-radius: 4px;\n  width: 400px;\n  height: 40px;\n  box-sizing: border-box;\n  padding: 8px 8px 8px 12px;\n}\n\n.informationicon:hover+.info-tooltip {\n  visibility: visible;\n  transition: opacity 0.3s ease-out 0.5s;\n  opacity: 1;\n}\n\n.appimagelogodiv {\n  cursor: pointer;\n  box-sizing: border-box;\n  position: absolute;\n  width: 160px;\n  height: 160px;\n  left: 0px;\n  top: 22px;\n  border: 1px solid #BCBCBC;\n  border-radius: 16px;\n}\n\n.title-logo {\n  position: absolute;\n  width: 80px;\n  height: 83.11px;\n  left: 39.5px;\n  top: 38px;\n  opacity: 0.3;\n}\n\n.appimagediv {\n  position: absolute;\n  width: 160px;\n  height: 182px;\n  left: 820px;\n  top: 0px;\n}\n\n.informationicon {\n  position: absolute;\n  left: 65px;\n  top: 0px;\n  font-family: 'Material Design Icons';\n  font-size: 20px;\n  line-height: 18px;\n  color: #757575;\n}\n\n.combo {\n  position: absolute;\n  top: 22px;\n  box-sizing: border-box;\n\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  padding: 8px 8px 8px 12px;\n  gap: 322px;\n\n  width: 400px;\n  height: 40px;\n\n  border: 1px solid #BCBCBC;\n  border-radius: 4px;\n\n  flex: none;\n  order: 1;\n  align-self: stretch;\n  flex-grow: 0;\n}\n\n.textbox {\n  position: absolute;\n  margin-top: 22px;\n  box-sizing: border-box;\n  display: flex;\n  flex-