gn: center;\n  color: #FFFFFF;\n  background: #151233;\n  padding: 8px 16px;\n  border-radius: 4px;\n  border: none;\n  cursor: pointer;\n}\n\n.resetdiv {\n  display: flex;\n  flex-direction: row;\n  align-items: center;\n  padding: 8px 16px;\n  gap: 4px;\n\n  width: 73px;\n  height: 40px;\n\n  border-radius: 4px;\n\n  /* Inside auto layout */\n\n  flex: none;\n  order: 0;\n  flex-grow: 0;\n}\n\n.reset {\n  position: absolute;\n  left: 766px;\n  top: 24px;\n  font-weight: 500;\n  font-size: 16px;\n  text-align: center;\n  color: #757575;\n  display: flex;\n  align-items: center;\n  padding: 8px 16px;\n  border-radius: 4px;\n  margin-right: 4px;\n  border: none;\n  background: transparent;\n  cursor: pointer;\n}\n\n.headertext {\n  position: absolute;\n  width: 69px;\n  height: 23px;\n  left: 40px;\n  top: 32px;\n\n  font-style: normal;\n  font-weight: 500;\n  font-size: 20px;\n  line-height: 23px;\n  white-space: nowrap;\n  /* Plateau Navy */\n\n  color: #151233;\n}\n\n.header {\n  position: absolute;\n  width: 1020px;\n  height: 88px;\n  left: 180px;\n  top: 0px;\n}\n\n.footer {\n  display: flex;\n  flex-direction: row;\n  justify-content: flex-end;\n  align-items: flex-start;\n  gap: 8px;\n  position: absolute;\n  width: 1020px;\n  height: 88px;\n  left: 180px;\n  top: 652px;\n}\n\n.leftnav {\n  display: flex;\n  flex-direction: column;\n  align-items: flex-start;\n  padding: 40px 0px;\n\n  position: absolute;\n  width: 180px;\n  height: 100%;\n  left: 0px;\n  top: 0px;\n  background: #F5F5F5;\n  border-radius: 11px 0px 0px 11px;\n}\n\n.maindiv {\n\n  box-sizing: border-box;\n\n  position: absolute;\n  width: 1200px;\n  height: 740px;\n  left: 150px;\n  top: 55px;\n\n  background: #FFFFFF;\n\n  border: 1px solid #D5D5D5;\n  border-radius: 12px;\n}\n\n.info-tooltip {\n  visibility: hidden;\n  position: absolute;\n  background: #757575;\n  color: #FFFFFF;\n  font-size: 11px !important;\n  text-align: center;\n  border-radius: 4px;\n  letter-spacing: 0.2px;\n  padding: 6px 8px 6px 8px;\n  opacity: 0;\n  z-index: 1;\n}\n</style>\n\n<style>\n.select-lottie .items {\n  top: 41px;\n  color: #212121;\n  border-radius: 8px;\n  border: 1px solid #E3E3E3;\n  overflow: hidden;\n  background-color: #FFFFFF;\n  box-shadow: 1px 2px 4px rgba(0, 0, 0, 0.2);\n}\n\n.select-lottie .items .option {\n  color: #212121;\n  padding-left: 12px;\n  line-height: 32px;\n}\n\n.select-lottie .items .option:hover {\n  background-color: #2C5A86;\n  color: #FFFFFF\n}\n\n.add-new-lottie-in-options {\n  border: none;\n  background: none;\n  color: #616161;\n  font-size: 15px;\n  font-weight: 500;\n}\n</style>\n"],"sourceRoot":""}]);
